@import url('https://fonts.cdnfonts.com/css/minecraft-4');

:root {
    --mainColor:#9b7653;
}

html{
    background-color: skyblue;
}

header {
    background-color: var(--mainColor);
}

.container {
    width: 95%;
    /*background-color: green;*/
    margin: 0 auto;
    max-width: 1200px;
}

.container-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    width: 130px;
    padding: .5em 0;
}

.list {
    display: flex;
}

.listItem {
    margin-left: 2em;
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
    font-family: 'Minecraft', sans-serif;
}

.activeItem {
    color: blue;
    font-size: 1.5rem;
    margin-left: 2em;
    font-family: 'Minecraft', sans-serif;
    /*border-bottom: .1em solid white;*/
}

.icon {
    color: blue;
    font-size: 1.5rem !important;
    margin-right: 1em;
    margin-left: 1.5em;
}

@media screen and (max-width: 1100px) {
    .icon {
        margin-left: 1em;
    }
}

@media screen and (max-width: 768px) {
    html{
        font-size: 13px;
    }

    .container-flex {
        flex-direction: column;
    }

    .icon {
        margin: 1em 1.5em;
    }

    .logo {
        width: 150px;
    }
}

@media screen and (max-width: 568px) {
    html{
        font-size: 11px;
    }

    .list {
        flex-direction: column;
    }

    .listItem {
        margin-top: .2em;
    }

    .logo {
        width: 100px;
    }
}


