/* Navigation.css */

.navigation {
    position: fixed;
    top: 50%;
    left: -120px; /* Начальная позиция за экраном */
    width: 150px; /* Уменьшите ширину меню */
    padding: 5px; /* Уменьшите отступы внутри меню */
    background-color: yellow;
    border: 1px solid black;
    border-radius: 5px;
    transition: left 0.3s ease; /* Анимация появления/исчезания */
}

.navigation ul {
    list-style: none;
    padding: 0;
}

.navigation ul li {
    margin: 0;
    padding: 5px 0; /* Уменьшите вертикальные отступы между элементами */
}

.navigation:hover {
    left: 0; /* Показать меню при наведении курсора */
}

.navigation.visible {
    left: 0;
}