@import url('https://fonts.cdnfonts.com/css/minecraft-4');

.mainSection {
    display: flex;
    background-color: skyblue;
    justify-content: space-around;
    align-items: center;
}

html{
    background-color: skyblue;
}

.contentBox {
    width: 30%;
    /*background-color: yellow;*/
    padding-left: 1.5em;
    font-family: 'Minecraft', sans-serif;

}

.contentBox h1 {
    font-size: 2.5rem;
    text-align: center;
    color: yellow;
    font-family: 'Minecraft', sans-serif;

}

.contentBox p {
    font-size: 1.8rem;
    color: yellow;
    margin: 1em 0;
    font-family: 'Minecraft', sans-serif;

}

.btnBox {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 2em;
}

.btn {
    border: 2px solid transparent;
    border-radius: 2em;
    background-image: linear-gradient(#fff, #fff),
    radial-gradient(circle at bottom right, purple, orange);
    background-origin: border-box;
    background-clip: content-box, border-box;
}

.readMore {
    display: inline-block;
    font-size: 1.2rem;
    padding: .5em 1.5em;
    text-decoration: none;
    color: grey;
}

.imgContainer {
    width: 50%;
}

.imgContainer img {
    width: 90%;
    margin: 1em;
    display: block;
}

/* Chatbot */
#chatbot-container {
    position: fixed;
    bottom: 100px;
    right: 20px;
    border-radius: 20px;
    box-shadow: 0px 5px 15px rgba(0,0,0,0.1);
}

#chatbot-button {
    background-color: #4cafaa;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
}

#EndPoem {
    font-family: 'Minecraft', sans-serif;
}

#chatbot-window {
    display: none;
    position: fixed;
    bottom: 100px;
    right: 20px;
    width: 300px;
    height: 400px;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0px 5px 15px rgba(0,0,0,0.1);
}

#chatbot-window.show {
    display: block;
}

#chatbot-messages {
    height: 300px;
    margin-bottom: 10px;
    overflow-y: auto;
    background-color: rgb(199, 224, 216);
    border: 1px solid #ccc;
    border-radius: 20px;
}

.message {
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 15px;
    max-width: 80%;
}

.user {
    background-color: #f1f1f1;
    align-self: flex-end;
}

.bot {
    background-color: #4CAF50;
    color: white;
}

#user-input {
    width: 80%;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 15px;
    font-family: "Franklin Gothic Heavy", serif;
}

button {
    padding: 10px 20px;
    border-radius: 15px;
    background-color: #4cafaa;
    color: white;
    cursor: pointer;
}


@media screen and (max-width: 900px) {
    html {
        font-size: 14px;
    }
}

@media screen and (max-width: 768px) {
    html {
        font-size: 14px;
    }
    .mainSection {
        flex-direction: column;
        margin-top: 1em;
    }
    .contentBox {
        width: 90%;
        margin: 0 auto;
    }
    .contentBox h1{
        font-size: 2rem;
    }
    .contentBox {
        font-size: 1.1rem;
    }
    .imgContainer {
        width: 80%;
    }
}

@media screen and (max-width: 400px) {
    .imgContainer {
        width: 60%;
    }
}

