.services{
    width: 100%;
    background-color: skyblue;
    margin:1em 0;
    display: flex;
    justify-content: space-around;
}

h1{
    text-align: center;
    font-family: 'Minecraft', sans-serif;
    color: yellow;

}

.card{
    width: 25%;
    text-align: center;
    box-shadow: 0 0 3px black;
    padding: 1em;
    font-family: 'Minecraft', sans-serif;
    color: yellow;

}

.serviceImage {
    width: 280px;
    height: 280px;
    object-fit: contain;
}

@media screen and (max-width: 990px){
    .services{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    .card{
        width: 40%;
        margin-bottom: 2em;
        margin-right: 1em;
    }
    .serviceImage {
        width: 180px;
        height: 180px;
        object-fit: contain;
    }
}

@media screen and (max-width: 786px){
    .services{
        flex-direction: column;
    }
    .card{
        width: 70%;
        margin: 1em;
    }
}
